@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,400;0,500;0,700;1,400&display=swap);
.person{display:flex;flex-direction:column;justify-content:end;width:250px;height:300px;margin-right:20px;padding:15px;color:#a5bea573;border-radius:10px;border:2px solid #a5bea573;transition-property:-webkit-transform;transition-property:transform;transition-property:transform, -webkit-transform;transition-duration:0.5s}.person .hover-hide{margin:10px 0 0;display:none}.person:hover{-webkit-transform:scale(1.05);transform:scale(1.05);color:white;text-shadow:1px 1px 1px #768676;background-position:center}.person:hover .person__name-prewiew{display:none}.person:hover .hover-hide{display:block}.person:nth-of-type(1):hover{background-image:url(https://image.freepik.com/free-photo/pleasant-looking-serious-man-stands-in-profile-has-confident-expression-wears-casual-white-t-shirt_273609-16959.jpg)}.person:nth-of-type(2):hover{background-image:url(https://image.freepik.com/free-photo/indoor-shot-of-beautiful-happy-african-american-woman-smiling-cheerfully-keeping-her-arms-folded-relaxing-indoors-after-morning-lectures-at-university_273609-1270.jpg)}.person:nth-of-type(3):hover{background-image:url(https://image.freepik.com/free-photo/attractive-mixed-race-male-with-positive-smile-shows-white-teeth-keeps-hands-on-stomach-being-in-high-spirit-wears-white-shirt-rejoices-positive-moments-in-life-people-and-emotions-concept_273609-15527.jpg)}

*{box-sizing:border-box}body{background-color:#a5bea53b}.app{display:flex;flex-direction:row;justify-content:center;margin-top:30px;font-family:"Roboto", sans-serif}

